.landing{
  background-image: url("https://img.freepik.com/foto-gratis/sabrosos-italianos-griegos-ingredientes-alimentos-mediterraneos-vista-superior-vieja-mesa-rustica-verde-arriba_1220-1681.jpg?w=2000"); 
     background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    align-content: center;
    height: 100vh;
}

.backgroundCard{
    background: url("Menu.png");
    background-color: rgba(255, 228, 196, 0.419);
    background-position:absolute;
    box-shadow: 0.4em 1.3em 1.5em rgba(0,0,0,1),
    0.65em 1.6em 1.6em rgb(63, 62, 62),
    0.85em 1.8em 1.8em rgba(0,0,0,1);
    border-width: 0.2em;
    border-color: black;
    background-size: cover;
    margin-left:17vh;
    margin-top: 10vh;
    width: 23%;
    height: 56%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    border-bottom-right-radius: 3%;
    border-top-right-radius: 3%;
    overflow: hidden;
}

 .opac{
    width: 100%;
    height: 100%;
    display: flex; 
    flex-direction: column;
    align-self: auto;
    background-color: rgba(114, 122, 74, 0.452);
} 
.landingCard{
      padding-top: 4em;
    padding-left: 3em;
    padding-right: 3em;
    display: flex;
    flex-direction: column;
    align-content: space-around;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}


.content{
    justify-content: flex-start;
        justify-content: space-evenly;
    display: flex;
    margin-top: 1;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    align-items: flex-start;
}
.content h2{
    padding-left: 0.7rem;
    padding-bottom: 3em;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.btnLink button{
    height: 10vh;
    width: 10vh;
    background-size: cover;
    background-color: rgb(123, 157, 118);
    background-image:url(https://cdn-icons-png.flaticon.com/512/55/55267.png); 
    color: rgb(255, 255, 255);
    font-family: sans-serif; 
    font-weight: 700;
    font-style: italic;
    border: 1px;
    margin-top: 4em;
}
.backgroundCard:hover{
	transform: translateY(-30px);
	transition: 0.5s;}
  .btnLink button:hover{
    transform: translateY(20px);
    transform: rotate(360deg);
    transition: 1.5s;
}


     